/*
 * cowo-gridlist
 * menu
 */ 
 
.cowo-gridlist-menu { // WAS .jq_tabsMenu
	display: flex; // "causes child to behave as if floated left"
	
	// margin-bottom: $space025;
	margin-bottom: $spacing05;
	
	ul {
		display: flex;
		// justify-content: flex-end;
		// justify-content: flex-start; /* DEFAULT BEHAVIOR */
		// justify-content: space-around;
		// padding: $space025;
		
		// padding-bottom: $space025;
		}

	li {
		// width:64px; /* TEMP - DEMO */ 
		text-align:center; 
		margin-right: $spacing025;
		&:last-of-type {margin-right: 0px;}
		color: white;	
		
		a {
			color: inherit;
			display:block; /* */ 
			padding: $spacing025;
			background: $c_primary_dark; 				
			} // a
			
			&.is-active a {
				background:$c_primary_light; 
				color: $c_primary_dark; 
				
				&:hover { // FORCED BEHAVIOR - COULD JUST USE AN IMPORTANT ON TOP Y PUNTO ... 
					background: $c_primary_light; 
					color: $c_primary_dark; 					
					}
				
				} // is-active
				
			a:hover {
				// background:$c_primary_light; 
				background:$c_primary; 
				color: $c_white;
				// color: $c_primary_dark; 
				}
					 
		} // li

				
	} // cowo-gridlist-menu
	
	
	



/*
 * cowo-gridlist
 * items
 */ 
 
// MIGHT ALSO BE STANDALONE RWALLYY... NO NEED FOR THE SUPER WRAPPER PERHAPS... 
// is-list is the arbitrary starting point - could also have magazine layout, and article layout..

.cowo-gridlist-items { 
	
	ul { 
		
		li { // COMMON
			background-color:$c_primary_light;
			border: 2px solid $c_primary_dark;
			padding: $spacing; // TEST
			
			color:$c_primary_dark;
			} // li
		
		
		&.is-list { 
			
			li { 
				/* LAYOUT */
				margin-bottom: $spacing05;
				&:last-of-type {margin-bottom: 0px;}
				} // li
			
			} // is-list
		
		
		&.is-grid { 
			/* LAYOUT - CSS GRID */
			display: grid;
  		// grid-template-columns: auto 100px 1fr 2fr;
  		// grid-template-rows: 50px 5em min-content auto;
  		grid-template-columns: repeat(6, 1fr); /* 6 COLUMNS */
  		// grid-template-rows: 50px 5em min-content auto; // REALLY UNNECESSARY... 
			grid-gap: $spacing05;
			
			li { 
				/* PLACEHOLDER */
				}
		
			} // is-grid
	
		} // ul
	
	} // cowo-gridlist-items 	