/* 
 * 1 - (TEXT) SELECTION 
 */

@mixin text_selection {
  // background-color: var(--moss-c-b-3);
  // background-color: red; // TEST 
  background-color: $c_accent; // TEST 
  color: white;
}
  ::-moz-selection { /* Code for Firefox */ 
    @include text_selection;
  }
  ::selection {
    @include text_selection;
  }


/* 
 * 2 - caret (input)  
 */


// https://developer.mozilla.org/en-US/docs/Web/CSS/caret-color
// caret-color: $c_accent;
input, textarea {
  // caret-color: $c_accent; // does it apply only to the line ?????
  caret-color: var(--primary-color); // does it apply only to the line ?????
}



// this is all kinda like "re-braqding reset-.ish concept , nice "



