section {

  
	&.padding {
		@include paddingv($spacing);
		}
	
	&.padding_2 {
		@include paddingv($spacing4);
		}
  
	&.padding_3 {
		@include paddingv($spacing3);
		}
  
	&.padding_3_4 {
		// @include paddingv($spacing3);
