footer {
	// overflow:hidden;
	@extend .clearfix;
	
	.inner {
		// overflow:hidden;
		@extend .clearfix;
		padding: $spacing05;
		@include paddingv($spacing05); // paddingh managed by frame
		// text-align: center; // very unspecific    
    color:#fff; // very unspecific		
		}
			
	} // footer







		.footerContent {
			text-align: center; // very unspecific  ...  
			
			
			font-size: $fs_l;
			
			span {font-size: $fs_m;}
			
			
			img {
				width: 86px;
				vertical-align: middle;
				}
				
			} // footerContent			
			
			
			
			
			
			
			
			
			
.m_scrolltotop {
	// @include dev-border;
	
	display: flex;
	justify-content: center;
	
	
	
	a {
		 // @include dev-border;
		 color: inherit;
		 cursor: pointer;
		 &:hover {color: $c_primary_light;}
		
			
			
		// line height issue ....
		
		
			
			.icon {
				// @include dev-border;
				font-size: 32px;
				// line-height: 32px;
				// line-height: 26px;
				}
		
		
		}	// a		
	
	
	} // m_scrolltotop

			
			

	
	
	
	
	
	
	
	
.gridFooter {
	// @include dev-border;
	
	/* LAYOUT - OUTSIDE */
	margin-top: $spacing;
	
	/* LAYOUT - INSIDE */
  display: flex;
	justify-content: space-between;
	
	} // gridFooter	
	
	
	
	
	
	
.gridFooter-item {
	
	.title {
		font-weight: bold;
		font-size: 20px;
		margin-bottom: $spacing025;
		}
	
	.text {
		
		ul {}
		
		li {
			// margin-bottom: $space025;
			margin-bottom: $spacing0125;
			&:last-of-type {margin-bottom: 0px;}
			
			a {
				color: inherit;
				// text-decoration; 
				&:hover {text-decoration: underline;}
				} // a
			
			} // li

		
		} // text
	
	
	
	} // gridFooter-item	
	
	
	