				// color: $c_white;
				// color: $c_primary_dark;
				font-size: 12px;
				}


		// https://www.w3schools.com/css/tryit.asp?filename=trycss_buttons_basic
			.options {
				// border: 3px solid red; // DEV

				ul { // they are buttons, at leats, not list s.... 
					display: flex;
					// gap: 12px; // TEMP HACK							
					gap: 8px; // TEMP HACK							
					// border: 3px solid blue; 
					height: 100%;
					align-items: center; // OPTIONAL .................
				} 

				li {
					
					// border: 1px solid red;
					
					/*
					margin-right: 10px;
					&:last-of-type {margin-right: 0;}
					*/

					a { // EXTEND A BUTTON CLASS MEJOR !!!!!!!!!!!!!!!!
