						// padding: 5px 10px;
						padding: 4px 8px;
						// width: 100px;
						min-width: 100px;
						min-width: 60px;
						text-align: center;

						font-size: 12px;
						color: #fff;

						@include transition(background-color, 0.5s); // sass it !!!!!!!!
						background-color: $c_primary;

						&:hover {
							// background-color: darken($c_green, 5%);
							background-color: $c_primary_dark;
							}

						} // a


					} // li


				} // options
		
		

			} // leydecookies_content



	
	
		@media only screen and (max-width: $bp_l) {

			
		} // MEDIA-QUERY


		@media only screen and (max-width: $bp_s) {
			background-color: transparent; /* RESET */
			border-top-width: 0px; /* RESET */
			
			.inner {
				// border: 3px solid blue; // s_border
				background-color: $c_primary_light;
				border: 3px solid $c_primary_dark; // OJO, IS BORDER BEING ADDEED UP TO THE BOX BORDER ??? 
				// outline: 3px solid $c_primary_dark; // OJO, IS BORDER BEING ADDEED UP TO THE BOX BORDER ??? 
