				flex-direction: column;
				align-items: stretch; // TEST // vip - tiene sentido , stretch en el sentido particualr
				
				.options {
					// border: 3px solid yellow;
					ul {
					// stretch: 1; // TEST 
					/* align-items: center; */
					// align-items: stretch;
					} // ul
					
					li {
						// stretch: 1; // TEST 
						// align-self:stretch;
						flex: 1; // vip
					} // li
					
					
				} // options


			} // inner




