/* 
 * CSS COUNTER 
 */
.cowo-accordion .accordion-pair .accordion-trigger::before {
  counter-increment: accordion-trigger;                   /* Increment the value of section counter by 1 */
  /* content: "# " counter(section) ": "; */   /* Display the word 'Section ', the value of section counter, and a colon before the content of each h3 */
	/* content: "# " counter(section); */ 
	/* content: counter(section);  */
	content: "Accordion-Trigger " counter(accordion-trigger);
}





/* CONTAINER */
.cowo-accordion {

	.accordion-pair { 
		margin-bottom: $spacing075;
		&:last-of-type {margin-bottom: 0px;}
	
		.accordion-trigger {
			cursor: pointer; 	
			// background-color:$c_primary_dark;
			background-color:$c_primary;
			padding: $spacing025;
			color: white;	 // TODO - UNIFY VARIANBL EUSAGE 
			
			&:hover {
				// background:$c_primary; 
				background:$c_primary_dark; 
				// color: $c_white;
				}
			
			
			&.is-active {
				background:$c_primary_light; 
				color: $c_primary_dark; 
				
				&:hover { // FORCED BEHAVIOR - COULD JUST USE AN IMPORTANT ON TOP Y PUNTO ... 
					background: $c_primary_light; 
					color: $c_primary_dark; 					
					}
						
				} // is-active		
				

									
		} // accordion-trigger

		
		/* HIDDEN SECTION */
		.accordion-target { // TARGET - AS TOP BOTTOM HACE PRESUNCIONES...  // was accordion_bottom
			/* display:none; */ /* DO WITH CSS OR WITH JS.. simpler to be done through css, it's more failproof */
		
			// margin-top: $space025;
			
			background:$c_primary_light;
			padding: $spacing;
			min-height:200px; /* DEMO */
			// color: $c_white; // USE "white", its cleaner... and "black" as well, less si more.	
			color: $c_primary_dark; 
			}
			
	} // accordion-pair
	
	
	

} // accordion_wrapper







