/*
 * CSS
 * Header - Nav
 */

nav.main {
	
	@media only screen and (max-width: $bp_m) {display: none;} /* MEDIA-QUERY */
	
	margin-left: auto; // HACKY, FLEX, its not hacky, its good felxbox
	
	
	.inner {
		@include paddingv($spacing05); // paddingh is managed by the frame
		position:relative; // ???
  }
	
	
	/* LEVEL #1 */	
	ul {
		display: flex;
		// gap: $space05; // reahcnhe rpto spacing - and then to css native variable system ... 
		// gap: $spacing05; // reahcnhe rpto spacing - and then to css native variable system ... 
		gap: $spacing025; 
		}
	
		li {
			
			/*
			margin-right: $space05;
			&:last-of-type {margin-right: 0px;}
			*/ 
			
			
			/* TEXT */
			a {
				/* LAYOUT */
				display: block;
				// padding: $space025 $space05;
				// spadding: $spacing025 $spacing025;
				padding: $spacing025 $spacing05;
				// padding: $space025 0px;
				/* TEXT */
				color: $c_white;
				font-size: $fs_l;
				// font-size: 24px;
				font-size: 20px;
					
							
				&:hover/* ,
				&.is-current */ {text-decoration: underline;}
				
				} // a
			
				
			
		// add second level, and 3rd level ... form nli copy !!!!!
						
				&:hover ul {display: block;} /* SHOW NEXT LEVEL */
				
				/* LEVEL #2 */		
				ul {
					/* UI */
					display: none; /* UI - SHOW ON HOVER : CSS */
					
					/* LAYOUT - EXTERNAL */
					position: absolute;
					
					/* LAYOUT - INTERNAL */
					// background-color: $c_primary; // AKA c_header 
          background-color: var(--primary-color);  
          
          
          
					// padding-top: 42px; // TEST
					// padding-bottom: 36px; 
					// padding-bottom: 24px;  // ?? usage ????
					// spadding-bottom: 12px;  // ?? usage ????
					padding-bottom: 8px;  // ?? usage ????
          
          
          
					
					li {
						float: none; /* RESET */
						display: block;
						margin-right: 0%; // RESET 
						
						
						a:hover {
							text-decoration: underline; // DEV TEST
							}
						
											
						/* 3rd LEVEL */
						ul {
							// border: 3px solid red; // DEV
							padding-top: 0px; // RESET
							position: relative; // RESET
							padding-bottom: 0px; // RESET
							// margin-left: $space05; // TEST
							margin-left: $spacing075; // TEST
							} // ul
	
						} // li
					
					
						// https://stackoverflow.com/questions/25952496/prevent-nested-nav-items-from-breaking-on-to-the-next-line
						li a {
							white-space: nowrap;
							// border: 1px solid red; 
							// width: 100%;	
							}
			
					} // ul
					
				
				
						
			} // li 
	


	
	
	

	
	
	
	
	

	
	} // nav.main
	




	




