.dev-grid-columns {
	/* DEV */
	border: 3px solid red;
	
	/* LAYOUT */
	// MIXIN FOR THIS
	// position: absolute; 
	position: fixed; 
	top: 0px;
	width: 100%; 
	height: 100%;
	
	
	/* LAYOUT - GRID */
	display: grid;
	
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  // grid-template-rows: 10% 10% 20% 10% 10% 10% 10% 10% 10% 10%;
	
	grid-gap: 24px;
	
	
	/* GENERIC INDIVIDUAL ITEM, FOR THE DEMO, FOR DEMO PURPOSES. */
	.item {	
		cursor: pointer;
		background-color: #ccc;	
		/* border: 3px solid black; */
		box-shadow: 0px 0px 2px 1px #000;
		
		font-size: 32px;
		text-align: center; /* no suple al flex */
		
		/* LAYOUT - FLEX */
		display: flex;
		justify-content: center;
		align-items: center;
	
		/* https://developer.mozilla.org/en-US/docs/Web/CSS/grid-auto-flow */
		
		/*
		grid-auto-flow: column;
		grid-auto-flow: row;
		grid-auto-flow: row dense;
		*/
		/* css counter, as marker for the fornt end , nice !! visyallly nice tool */
		} /// ITEM




	} // dev-grid
	
	



/* MIXIN TO DECLARE-CSS-COUNTER */
.dev-grid-columns .item::before {
  counter-increment: section;                   /* Increment the value of section counter by 1 */
  /* content: "# " counter(section) ": "; */   /* Display the word 'Section ', the value of section counter, and a colon before the content of each h3 */
	/* content: "# " counter(section); */ 
	content: counter(section); 
}
	