/* =================================================================== */
/* LIGHTBOX ME                                       				   */
/* =================================================================== */


.cowo-lightboxme-trigger {
	@extend .buttonBig;		
	
	// VIP
	display: inline-block;
	} // lightboxme_trigger




.cowo-lightboxme-target {
	display:none; // UI - Show with jQuery 
	/* width:700px; */
	width:62%; 
	border: 3px solid $c_primary_dark; // MIXIN FOR A GENERIC BORDER
	// background-color:$c_primary_light; 	
	} // cowo-lightboxme-target
	




	.cowo-lightboxme-content {
		// clear:right; // !!!!!!!!!!!!!!!!!!!
		
		/* COMMON STYLE */
		background-color:$c_primary_light; 
		padding: $spacing;
		color: $c_primary_dark; 	
		} // cowo-lightboxme-content



	.cowo-lightboxme-close {
		position: relative;
		
		.close { /* ".close" for lightboxme script */
			cursor:pointer; // UI
			

			position: absolute; top: -36px; right: -32px;
	
			/* CIRCLE */
			@include circle(64px);
			// background-color: $c_primary; // !!!
			
			text-align:center; // VS FLEX ... 
			
			padding-top: 14px; // TEST 
			font-size: 32px;
			// @include vcenter;
			
			// TEST
			@extend .buttonBig;		
			
			&:hover {
				// color:$c_primary;
				}
						
			} // close
		
		} // cowo-lightboxme-close
		
		
		
		


		
		
		
		
		