/* CREATE MIXIN for rem fontsize and px fallback solution */


/* FALLBACK for background opacity */

// create mixin - CSS_FALLBACK_tricks - LINK - the RGB fallback color only works when using shorthand. 
// http://css-tricks.com/ie-background-rgb-bug/

// call
// @include background-opacity($c_primary_light, 0.25);
// me suena que tenai que ser background y no backgroound-color
@mixin background-opacity($color, $opacity) { // bg-opacity
	background: $color; /* Fall-back for brosers that don't support opacity */
	background: rgba($color, 0.85);
	}







