 * CSS
 * cowo-dropdown
 */ 
 				
.cowo-dropdown { 
	
	.dropdown-pair { 
		margin-bottom: $spacing05;
		&:last-of-type {margin-bottom: 0px;}
	
		.dropdown-header {
			/* PLACEHOLDER */		
			} // dropdown-header

		
			.dropdown-trigger { 
				cursor: pointer; 	
				background-color:$c_primary;
				padding: $spacing025;
				color: white;				
			
				// MOVE UP A BIT, ALSO THE ONE FOR THE TABS , AS IT'LL BE VISUALLY CLEARER
				
				/* STATE - HOVER */
				&:hover {
					background:$c_primary_dark; 
					color: $c_white;
					}
				
				/* STATE - ACTIVE */
				&.is-active {
					background:$c_primary_light; 
					color: $c_primary_dark; 
					
					&:hover { // FORCED BEHAVIOR - COULD JUST USE AN IMPORTANT ON TOP Y PUNTO ... 
						background: $c_primary_light; 
						color: $c_primary_dark; 					
						}
							
					} // is-active		
				
			
				} // dropdown-trigger
				
				
				
				
				
		
		
		/* HIDDEN SECTION */
		.dropdown-target { // TARGET - AS TOP BOTTOM HACE PRESUNCIONES...  // was accordion_bottom
			display:none; /* DO WITH CSS OR WITH JS.. simpler to be done through css, it's more failproof */
		
			// margin-top: $space025;
			
			background:$c_primary_light;
			padding: $spacing;
			min-height:200px; /* DEMO */
			// color: $c_white; // USE "white", its cleaner... and "black" as well, less si more.	
			color: $c_primary_dark; 
			} // dropdown-target
			
			
			
		} // dropdown-pair
	
	
	} // cowo-dropdown
	
	
			
		
		.genericDropdownItem_top {
			position:relative; /* for the trigger */
			padding-top:5px;
			background-color:$c_primary;
			min-height:50px;
			
			.genericDropdownItem_top_trigger {
				position:absolute; bottom:0px; right:0px;
				background-color:$c_primary;
				font-weight:bold;		
				
				.genericDropdownItem_top_trigger:hover,
				.genericDropdownItem_top_trigger.is_active {
					background-color:$c_primary; /* TESTING NEW THING */    /* SASS IT !!!!!!!!!!!!!! */
					color:$c_white;
					}	// UI
					
											
				} // genericDropdownItem_top_trigger
				
							
			}	// genericDropdownItem_top				
										

				

						

			
							
		
		
/* FUNCTION */		
.jq_dropdown {/* THE PARENT OF EACH DROPDOWN TOP/BOTTOM PAIR */}

	.jq_dropdown_shown {}

		// .jq_dropdown_shown.is_active {background-color:$c_primary;}	
			
			// .jq_dropdown_trigger {cursor:pointer;}			
				
				
				.jq_dropdown_trigger.is_active {
					/*
					background-color:$c_primary_dark;
					color:$c_white;
					*/
					background-color:$c_primary_light;
					color:$c_primary_dark;
					}	
					
					
					.jq_dropdown_trigger.is_active:hover {
						color:#000;
						}
									
			
