/* 
 * CSS COUNTER 
 */
// .accordion-header::before { // CANOT BE BY OTSELF, IT MUST BE COUNTED INSIDE A WRAPPER... I GUESS, I HOPE... 
.accordion-container .accordion-header::before {
  counter-increment: vallenato;                   /* Increment the value of section counter by 1 */
  /* content: "# " counter(section) ": "; */   /* Display the word 'Section ', the value of section counter, and a colon before the content of each h3 */
	/* content: "# " counter(section); */ 
	/* content: counter(section);  */
	content: "Accordion " counter(vallenato);
}







// thisi system doesnt containe accordion-pair... but i guess i'll manage

.accordion-container {
	// @include border;
	
	.accordion-header {
		cursor: pointer; // UI
		background-color:$c_primary_dark;
		// background-color: #ccc;
		padding: $spacing025;
		color: white;	
		
		/* SEPARATION BETWEEN ITEMS - solved without needing to build an "accordin pair" ...  but still */
		// margin: 5px 0 0 0;
		margin-top: $spacing05;
		&:first-of-type {margin-top: 0px;}
		}	// accordion-header 
	
	
	
	
		.active-header {

			// @include border;
			// border: 3px solid black;

			/*
			-moz-border-radius: 5px 5px 0 0;
			-webkit-border-radius: 5px 5px 0 0;
			border-radius: 5px 5px 0 0;
			*/
			// background: url(images/active-header.gif) #cef98d;
			// background: url(../img/icons/images-vallenato/active-header.gif) #cef98d;
			background: url(../img/icons/images-vallenato/active-header.gif) $c_primary_dark;
