/* input[type=file] & label trick */
/* THE HTML
        <label class="filebutton">
        Seleccionar arxiu
        <span><input type="file" name="doiser" id="dosier" /></span>
        </label>
*/	

label.filebutton {
    width:120px;
    height:40px;
    overflow:hidden;
    position:relative;
    background-color:#fff;
	border:1px solid #ccc;
	padding:0 5px;
}
label.filebutton:hover {
    background-color:#ddd;
}

label span input {
    z-index: 999;
    line-height: 0;
    /* font-size: 50px; */
	font-size: 15px;
    position: absolute;
    /* top: -2px; */
	top: -4px;
    /* left: -700px; */
	left: -245px;
    opacity: 0;
    filter: alpha(opacity = 0);
    -ms-filter: "alpha(opacity=0)";
    cursor: pointer;
    /* _cursor: hand; */
    margin: 0;
    padding:0;
}
/* END OF - FORM - label covering input[type=file] */

