  a {
    color: --primary-text; // cudl be a rule,., / --C-TEXT / C-TEXT-2
    @include a_ui_default;
  } // a
  
  
  
  
  
  span {
    
    /*
    &:not(:last-of-type) {
      color: #ff0000;
      
      &::after { //s not last, asi me ebvito la revsision de la excecpcin !!!!
        content: '>';
        margin-left: 0.3em;
      } // after
       
    }
    */
    
    &::after { // not last, asi me ebvito la revsision de la excecpcin !!!!
      content: '>';
      margin-left: 0.3em;
    } // afterss
    
    &:last-of-type {
      &::after { //s not last, asi me ebvito la revsision de la excecpcin !!!!
        content: '';
        margin-left: unset;
      } // after
    } // last
    
  } // span
  
  
  
  
  
  
  
  
} // breadcrumbs


















