.grid_contact {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: $spacing4; // TEST 
  
  
  @media only screen and (max-width: $bp_m) { 
    grid-template-columns: repeat(1, 1fr);
    gap: $spacing2; // TEST 
  }	// MEDIA-QUERY
  
  
  
  // contact_text ... / or just a generic "nig text, but nor extcerpt porque eso tiene mas css asociado ...."
  
  
  .contact_details {
    // border: 3px solid red;     
    @include paddingv($spacing);
    
    // hay rows ... lis ...
    
    ul {
      display: flex;
      flex-direction: column;
      // align-items: center;
      gap: 12px; // TEST 
    }
    
    
    li {
      display: flex;
      align-items: center;
      // gap: 12px; // TEST 
      gap: 6px; // TEST 
      
      a {
         
        @include a_ui_default; 
        
      } // a
      
      
      
    } // li
    
    
    
    
    
  } // contact_details
  
  
  
  
} // grid_contact
























