/* 
 * GRID - STAFF
 */
.grid_staff { // m_staff / rename to grid_staff / same in oslo, and then move to wp_eimoe, without categories / ANSATTE IN NORWEGIAN 
  // border: 2px solid black;
  
  
  // @include paddingv($spacing);
  // @include paddingv($spacing2);
  // @include paddingv(36px);
  // @include paddingh($spacing2);

  display: grid;
  grid-template-columns: repeat(4, 1fr); // LO DEL MINMAX .... 
  // gap: 24px;
  gap: 36px;

  // padding-bottom: $spacing3; // TEST - VARIABLE 
  
  @media only screen and (max-width: $bp_l) { 
    grid-template-columns: repeat(4, 1fr);
    gap: 24px;
  } // MEDIA-QUERY	
  @media only screen and (max-width: $bp_m) { 
    grid-template-columns: repeat(3, 1fr);
  } // MEDIA-QUERY	
  @media only screen and (max-width: $bp_s) { 
    grid-template-columns: repeat(2, 1fr);
  } // MEDIA-QUERY	
  @media only screen and (max-width: $bp_xs) { 
    grid-template-columns: repeat(1, 1fr);
    
    row-gap: 48px;
    
  } // MEDIA-QUERY	
  
  
} // m_staff




