/* 
 * CSS COUNTER 
 */
.cowo-tabs-menu ul li a::before {
  counter-increment: tab;                   /* Increment the value of section counter by 1 */
  /* content: "# " counter(section) ": "; */   /* Display the word 'Section ', the value of section counter, and a colon before the content of each h3 */
	/* content: "# " counter(section); */ 
	/* content: counter(section);  */
	content: "Tab " counter(tab);
}





.cowo-tabs {
			
 .tab {
	// clear:both; /* ??? */
	background:$c_primary_light;
	padding: $spacing;
	min-height:200px; /* DEMO */
	// color: $c_white; // USE "white", its cleaner... and "black" as well, less si more.	
	color: $c_primary_dark; 
	}
	
} // cowo-tabs 
	
	

		
/*
 * TABS MENU   - design it materlia styla, like the one in the cowoCMS ..   
 // NEEDNOT BE LOCATED INSIDE THE OTHER WRAPPER, ITS FREE.. 
 */
 
.cowo-tabs-menu { // WAS .jq_tabsMenu
	display: flex; // "causes child to behave as if floated left"

	ul {
		display: flex;
		// justify-content: flex-end;
		// justify-content: flex-start; /* DEFAULT BEHAVIOR */
		// justify-content: space-around;
		// padding: $space025;
		padding-bottom: $spacing025;
		}

	li {
		width:64px; /* TEMP - DEMO */ 
		text-align:center; 
		margin-right: $spacing025;
		&:last-of-type {margin-right: 0px;}
		color: white;	
		
		a {
			color: inherit;
			display:block; /* */ 
			padding: $spacing025;
			// background: $c_primary_dark; 				
			background: $c_primary; 				
			} // a

						
			a:hover {
				background:$c_primary_dark; 
				color: $c_white;
				}
		
		
			&.is-active a {
				background:$c_primary_light; 
				color: $c_primary_dark; 
				
				&:hover { // FORCED BEHAVIOR - COULD JUST USE AN IMPORTANT ON TOP Y PUNTO ... 
					background: $c_primary_light; 
					color: $c_primary_dark; 					
					}
				
				} // is-active

					 
		} // li

				
	} // .cowo-tabs-menu
	
	
	

				
			

@media only screen and (max-width: 600px) { 
		
.cowo-tabs-menu li {
	width:23.5%; 
	margin-right:2%; 
	color:#FF0 !important;
	
	&:last-of-type {margin-right:0%;}
	
	}

	
	
} /* MEDIA-QUERY - width is inclusive */


@media only screen and (max-width: 380px) {

.cowo-tabs-menu li a {font-size:0.75rem; }	
	
} /* MEDIA-QUERY - width is inclusive */